import React from "react";
import styled from 'styled-components'
import Logo from './Logo'
import Nav from "./Nav";

interface TopMenuProps {
    isSticky: boolean,
    distanceFromTop: number
}

const TopMenu: React.FC<TopMenuProps> = ({ isSticky, distanceFromTop }) => {
    return (
        <StyleTopMenu isSticky={isSticky} distanceFromTop={distanceFromTop}>
            <StyledContainer>
                <StyledTopMenuInner isSticky={isSticky} distanceFromTop={distanceFromTop}>
                    <div style={{ flex: 1 }}>
                        <Logo />
                    </div>
                    <Nav />
                </StyledTopMenuInner>
            </StyledContainer>
        </StyleTopMenu>
    )
}

interface StyledTopMenuProps {
    isSticky: boolean,
    distanceFromTop: number
}

const StyleTopMenu = styled.div<StyledTopMenuProps>`
    position: absolute;
    z-index: 99;
    width: 100%;
    background-color: ${props => props.distanceFromTop < -150 ? props.theme.stickyTopBarBG : props.theme.topBarBG};
    opacity: 0.9;
    transition: all 0.3s;
`

const StyledContainer = styled.div`
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1000px;
    padding: 0 10px;
    width: 100%;
`
const StyledTopMenuInner = styled.div<StyledTopMenuProps>`
    align-items: center;
    display: flex;
    height: ${props => props.distanceFromTop < -150 ? props.theme.stickyTopBarSize : props.theme.topBarSize}px;
    justify-content: space-between;
    max-width: ${props => props.theme.siteWidth}px;
    width: 100%;
    flex-wrap: wrap;
    transition: all 0.3s;
    
    @media (max-width: 768px) {
        display: flex;
        justify-content: center;
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        height: 80px;
        padding-top: 20px;
        padding-bottom: 20px;
    }
`

export default TopMenu;
