import React, { useEffect } from 'react'
import styled from 'styled-components'
import { inception } from "../../assets/js/inception"


const Builder: React.FC = () => {
    useEffect(() => {
        const setCityInterval = inception(document, window);
        // window.onfocus = function () {
        //     console.log('test-in');
        //     clearTimeout((window as any).myInterval);
        //     setCityInterval();
        // };
        // window.onblur = function () {
        //     console.log('test-out');
        //     clearTimeout((window as any).myInterval);
        // };

        return () => clearTimeout((window as any).myInterval);
    }, [])
    return (
        <StyleCanvas id='city' />
    )
}

const StyleCanvas = styled.canvas`
    margin: 0 10%;
    width: 80%;
    height: calc( 100vh - 400px);
    @media (max-width: 768px) {
        flex-flow: column nowrap;
        align-items: center;
    }
`;

export default Builder
