const StyledPage = styled.div`
    background-image: url(${bg_home});
    padding-top:24px;
    height:100vh;
    position:absolute;
`
const StyledSection = styled.div`
    width:100%;
    height:100vh;
    overflow:hidden;
    @media (max-width: 768px) {
        width:100%;
        height:auto;
        overflow-y:auto;
        overflow-x:hidden;
    }
`

const StyledMain = styled.div`
    align-items: center;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - ${props => props.theme.topBarSize * 2}px);
    background-image: url(${bg_home});
    
`

const StyledIcons = styled.div`
    align-items: center;
    display: flex;
    justify-content: center;
`

const StyledIcon = styled.a`
    align-items: center;
    outline:none;
    display: flex;
    justify-content: center;
    font-weight: 500;
    text-decoration: none;
    text-align: center;
    padding-top: 50px;
    color: #FFF;
    transition: all 0.3s;
    font-size: 25px;
    pointer-events: undefined;
    cursor: pointer;
    &:hover {
        transform: scale(1.06, 1.06);
    }
`

interface CardWrapperProps {
    width?: string;
    isBorder?: boolean;
    bgColor?: string;
    margin?: string;
}

const HeaderWrapper = styled.div<CardWrapperProps>`
    display: flex;
    justify-content: center;
    width: ${props => props.width};
    text-align: center;
    @media (max-width: 768px) {
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
    }
`;

const SectionWrapper = styled.div<CardWrapperProps>`
    display: flex;
    justify-content: center;
    flex-flow: column nowrap;
    align-items: center;
    width: 100%;
    border-bottom: 0px solid ${props => props.isBorder ? '#F0B90B' : 'transparent'};
    background-color: transparent;
`;

const InnovationWrapper = styled.div<CardWrapperProps>`
    margin-top: 0px;
    display: block;
    width: 85%;
    border-bottom: 0px solid ${props => props.isBorder ? '#F0B90B' : 'transparent'};
    background-color: transparent;
`;


const TechCardWrapper = styled.div<CardWrapperProps>`
    display: flex;
    justify-content: center;
    margin: ${props => props.margin};
    width: calc( 50% - 20px );
    float:left;
    border-bottom: 5px solid ${props => props.isBorder ? '#F0B90B' : 'transparent'};
    background: black linear-gradient(90deg,#1f1f1f,#000);
    border: 5px solid #000;
    border-radius: 12px;
    transition: all .3s;
    border: 5px solid #027561;
    &:hover {
        border: 5px solid #08F3CB;
    }
    
    @media (max-width: 768px) {
        width:100%;
        margin: 10px 0;
    }
`;

