import React from 'react'
import styled from 'styled-components'
import { NavLink } from "react-router-dom";


const Nav: React.FC = () => {
    return (
        <StyledNav>
            <StyledLink href="https://docsend.com/view/wbknd84pehb7vxs2" target="_blank">Whitepaper</StyledLink>
            <StyledLink href="https://docs.titi.finance/" target="_blank">Docs</StyledLink>
            <StyledLink href="https://docsend.com/view/v9s26v6r4bb2vf8s" target="_blank">Deck</StyledLink>
            <StyledLink href="https://blog.titi.finance/" target="_blank">Blog</StyledLink>
            <StyledLink href="https://discord.gg/fWyj7rwvR3" target="_blank">Community</StyledLink>
            {/* <StyledLaunchLink exact activeClassName="active" to="/mamms">Launch App</StyledLaunchLink> */}
            <StyledLaunchLink href="https://twitter.com/TiTiProtocol" target="_blank">Launch App</StyledLaunchLink>
        </StyledNav>
    )
}

const StyledNav = styled.nav`
    grid-auto-flow: column;
    column-gap: ${props => props.theme.spacing[5]}px;
    display: grid;
    @media (max-width: 768px) {
        column-gap: 12px;
    }
`
const StyledLink = styled.a`
    color: ${props => props.theme.color.grey[100]};
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
    transition: all 0.3s;
    border-radius: 8px;
    align-self:center;
    justify-self:center;
    &:hover {
        color: #08F3CB;
    }
`
